<template>
  <AppCard
    v-if="data && orderSchema"
    basis="500px"
    :title="'Информация о заказе #' + data.id"
  >
    <FormKit
      v-model="data"
      type="form"
      :actions="false"
    >
      <FormKitSchema :schema="orderSchema" />
    </FormKit>
  </AppCard>
</template>

<script setup lang="ts">
import AppCard from '@/components/markup/AppCard.vue';
import {ListItemService} from '@/services/list-item.service';

const props = defineProps<{
  data: OrderFormData;
}>();
const data = ref(null as null | OrderFormData);
const orderSchema = ref(null as null | any[]);
const service = useService(ListItemService);

onMounted(async () => {
  const options = await service.getEnabledSippingMethods();

  orderSchema.value = [
    {
      $formkit: 'primeCheckbox',
      name: 'vatDocumentRequired',
      label: 'Требуется документ для VAT',
      _disabled: true,
    },
    {
      $formkit: 'primeDropdown',
      name: 'shippingMethodId',
      label: 'Способ получения',
      validation: 'required',
      _disabled: true,
      options,
    },
    {
      $formkit: 'primeTextarea',
      name: 'comment',
      label: 'Коментарий',
      rows: '5',
      _disabled: true,
    },
  ];
});

onUpdated(() => {
  data.value = Object.assign({}, props.data);
});

</script>

<script lang="ts">
export interface OrderFormData {
  id: number;
  vatDocumentRequired: boolean;
  shippingMethodId: number;
  comment: string;
}
</script>
